<template>
  <el-container>
    <el-main>
      <el-table :data="userList">
        <el-table-column prop="userName" label="角色姓名" />
        <el-table-column prop="state" label="角色状态" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click="stopClick(scope.row)" type="text" size="small">停用</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>

      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
import { systemroleInfo } from "../../../../data/systemManage";
export default {
  name: "systemRoletable",
  data() {
    return {
      userList: Array(8).fill(systemroleInfo),
      dialogVisible: false,
      dialogContent:'',
      currentPage: 1
    };
  },
  methods: {
    handleClick() {
      var datas = {
        title:'信息编辑',
        state: true
      }
      this.$emit('dialogState', datas);
    },
    stopClick(){
      this.dialogContent = '确认停用该用户信息?'
      this.dialogVisible = true;
    },
    delMsg() {
      this.dialogContent = '确认删除该用户信息?'
      this.dialogVisible = true;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }    
  }
};
</script>
<style lang="less" scoped>
</style>