<template>
    <div class="systemUser">
        <el-button type="primary" class="btn" @click="addUser">新增角色</el-button>
        <system-roletable @dialogState="dialogState"/>
        <system-roleadd v-if="state" :header="title" :data="data" @dialogState="dialogState"/>
    </div>
</template>
<script>
import SystemRoletable from './controls/systemRoletable'
import systemRoleadd from './controls/systemRoleadd'

export default {
  name: 'systemRole',
  data() {
    return {
      title:'',
      data:'',
      state: false
    }
  },
  methods: {
    dialogState(data){
      data = data || {};
      this.state = data.state;
      this.title = data.title || this.title;
      this.data = data.data;
    },
    addUser(){
      this.$router.push("roleAdd?type=add");
    }
  },
  components: {
    SystemRoletable,
    systemRoleadd
  }
}
</script>
<style lang="less" scoped>
.systemUser{
    .btn{
        margin-left:20px;
    }
}
</style>
